@import url(https://fonts.googleapis.com/css2?family=Rubik+Mono+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rubik+Mono+One&family=Ubuntu:wght@300;400;700&display=swap);
*{
    margin: 0;
    padding: 0;
    font-family: 'Ubuntu', sans-serif;
    /*font-family: 'Rubik Mono One', sans-serif;*/
}
body{
    background-color: #1B314D;
}
.container{
    margin-top: 0px;
    padding: 20px;
    font-size: 14px;
}
.board{
    opacity: 0.2;
    transition: ease-out 1s;
    padding: 0px;
    margin: auto;
    color: honeydew;
    max-width: 800px;
    font-size: 2.5em;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    background-color: #286699;
}
#reloadbutton{
    display: none;
}
.board .carddiv{
    background: transparent;
    z-index: 1000;
    cursor: pointer;
    width: 100%;
    height: 100%;
}
.board .card{
    z-index: -900;
    position: relative;
    transition: transform 1s;
    transform-style: preserve-3d;
    text-align: center;
    color: #286699;
    height: 1.5em;
    width: 100%;
    margin-top: 20px;
    border-radius: 5px;
    background-color: #edd202;
}
.card .card-face{
    position: absolute;
    height: 100%;
    width: 100%;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden; /* COOL! */
}
.card .front{
    
    display: block;
}
.card .back{
    display: block;
    transform: rotateY(180deg);
}
.card.is-flipped{
    transform: rotateY(-180deg);
}
.board .col{
    
    perspective: 600px;
}
.board .row{
   
   padding-left: 20px;
   padding-right: 20px;
   
}

.gameboard{
    border-top: 1px solid #edd202;
    text-align: center;
    padding-bottom: 20px;
}
.boardinfo{
    box-shadow: inset 0 -20px 20px -20px #edd202;
    font-size: 1.3em;
    padding-left: 10px;
    padding-right: 10px;
    max-width: 800px;
    margin: auto;
}
.boardinfo .info{
    margin-bottom: 15px;
    font-weight: 400;
    color: #edd202;
    font-weight: 300;
}
.copy{
    font-size: 1.3em;
    margin-bottom: 15px;
    font-weight: 400;
    color: #edd202;
    font-weight: 300;
}
.boardinfo .buttondiv span{
    color: #edd202;
    font-family: 'Rubik Mono One', sans-serif;
}
